export default {
	data() {
		return {
			title: 'Hello',
			hottitlelist: ['热门标题111', '热门标题222', '热门标题333'],
			showlist: "",

		};
	},
	components: {
		articlebrief: articlebrief,
		mainpagetop,
		mainpageleft,
	},

	created: function() {
		var _this = this;
		uni.request({
			url: 'http://127.0.0.1:8080/queryAllArticles',
			method: "POST",
			success: (res) => {
				_this.showlist = res.data.data.rows;
