			},
			fail: (res) => {
				console.log("index unirequest fail");
				console.log(res);
			}
		});
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "诺圈"
		});
	},
	methods: {
		show() {
			if (this.showmenu == 0) {
				this.showmenu = 1;
			} else {
				this.showmenu = 0;
			}
		}
	}
};
